import {eId, eClass, darkLightMode, randomColor} from '../../../components/helpers/StylingHelpers';

function CourseScanStyle(checkPointNo){
    setDarkLightMode();
    setStyle(checkPointNo);

    var userAgent = navigator.userAgent || navigator.vendor || window.opera;
    if (!(/android/i.test(userAgent))) {
        window.addEventListener('resize', function(){
            setStyle(checkPointNo);
        });
    }
}

export default CourseScanStyle;

function setDarkLightMode(){
    darkLightMode('courseScan');
}

function setStyle(checkPointNo){
    eId('courseScan').style.height = eId('nav').offsetTop - eId('courseScan').offsetTop + 'px';
    eId('resultsChart').style.height = eId('nav').offsetTop - eId('resultsChart').offsetTop + 'px';

    setBar(checkPointNo);
}

function setBar(checkPointNo){
    if(!eClass('bar')){
        return false;
    }

    let bars = eClass('bar');
    
    for(let i = 0; i < bars.length; i++) {
        let checkpoints = bars[i].getElementsByClassName('checkpoint');
        
        for(let j = 0; j < checkpoints.length; j++) {
            checkpoints[j].style.width = bars[i].offsetWidth/(parseInt(checkPointNo) + 1) + 'px';
            checkpoints[j].style.height = '15px';
            
            if(j == 0){
                checkpoints[j].style.borderLeft = '2px solid #5aa2f9';
            }
            
            if(j == checkpoints.length - 1){
                checkpoints[j].style.borderRight = '2px solid #5aa2f9';
                checkpoints[j].getElementsByTagName('div')[0].style.borderRight = 0;
            }

            checkpoints[j].onmouseenter = function(){
                window.sessionStorage.setItem('showTooltip', '1');
                setTooltip(checkpoints[j]);
            }

            checkpoints[j].onmouseleave = function(){
                window.sessionStorage.removeItem('showTooltip');
                setTooltip(checkpoints[j]);
            };
            
        }
    }
}

function setTooltip(checkpoint){
    let tooltip = checkpoint.getElementsByClassName('tooltiptext')[0];
    
    if(window.sessionStorage.showTooltip == 1){
        tooltip.style.top = tooltip.offsetTop - tooltip.offsetHeight * 0.7 - eClass('tbody')[0].scrollTop + 'px';
        tooltip.style.left = tooltip.offsetLeft - tooltip.offsetWidth * 0.1 - eClass('tbody')[0].scrollLeft + 'px';
        tooltip.style.visibility = 'visible';
    } else {
        tooltip.style.top = 'initial';
        tooltip.style.left = 'initial';
        tooltip.style.visibility = 'hidden';
    }
}